
import {defineComponent} from "vue";
import useVuelidate from "@vuelidate/core";
import {required, email, minLength} from "@vuelidate/validators";

import BaseInput from "@/components/Forms/BaseInput.vue";
import BaseSelect from "@/components/Forms/BaseSelect.vue";

export default defineComponent({
    name: "VideoConfigCard",
    components: {
        BaseInput,
        BaseSelect,
    },
    setup() {
        let isAlertShow = false;
        const copyToClipBoard = (textToCopy: string) => {
            isAlertShow = !isAlertShow
            textToCopy = decodeHtml(textToCopy)
            navigator.clipboard.writeText(textToCopy);
            setTimeout(() => isAlertShow = false, 1300)
        }

        const decodeHtml = (html: string) => {
            let txt = document.createElement("textarea");
            txt.innerHTML = html;
            return txt.value;
        }
        return {
            v$: useVuelidate(),
            copyToClipBoard,
            decodeHtml,
            isAlertShow
        };
    },
    watch: {
        formData: {
            handler(value) {
                this.$emit("formData", value);
            },
            deep: true,
            immediate: true,
        },
    },
    data() {
        return {
            arrType: [
                'vslider',
                'inread',
                'sticky'
            ],
            arrPosition: [
                'bottom-left',
                'top-right',
                'top-left',
                'bottom-right',
            ],
            arrAdTimePosition: [
                'mid-roll',
                'post-roll',
            ],
            arrPassBackMode: [
                'banner',
                'video',
            ],
            formData: {
                zoneID: "",
                selectedType: "",
                selectedPosition: "",
                tagCallTime: "",
                selectedAdTimePosition: "",
                selectedPassBackMode: "",
                urlSourceVideo: "",
            },
            fields: [
                {tag: ''}
            ],
        };
    },
    validations() {
        return {
            formData: {
                zoneID: {required, minLength: minLength(4)},
                urlSourceVideo: {required, minLength: minLength(4)},
                selectedType: {required},
                selectedPosition: {required},
                selectedAdTimePosition: {required},
                selectedPassBackMode: {required},
                tagCallTime: {required},
            },
        };
    },
    methods: {
        clickHandler() {
            if (this.v$.$invalid) {
                return;
            }
            console.log(this.formData);
        },
        addField() {
            this.fields.push({tag: ''});
        },
        removeField() {
            if (this.fields.length > 1) {
                this.fields.pop();
            }
        },
    },
});
